<template lang="pug">
  div.wrapper
  
    About
    
    h5 Набранные баллы: {{score}}
    h5 Время: {{ timer.minutes }} : {{ timer.seconds }}
    
    //Игра
    p.getDataBtn.styleBtn(@click="skipWord") Пропустить слово
    div.rightWordWrapper
      input.rightWord(placeholder="Отгадайте слово" v-text="newLetters" v-model="newLetters" )
      div.delBtn.styleBtn(@click="deleteLastLetter" title="Сбросить") &#8656;
      div.delBtn.styleBtn(@click="deleteInputWord" title="Сбросить") &#10008;
      
    
    Preloader(v-show="!img_src")
    
    img.descriptionImg(:src="img_src")
    
    div.randomLetterWrapper
      div.randomLetter(@click="updateHiddenWord($event)" v-for="letter in shuffledWord") {{ letter.toUpperCase() }}
    <ModalTable />
    
    <ModalWindow @reRunGame="letStartTheGame"/>
</template>

<script>
  import * as axios from "axios";
  import About from "@/components/appAbout.vue"
  import ModalTable from "@/components/appModalTable.vue"
  import ModalWindow from "@/components/appModalWindow.vue"
  import Preloader from "@/components/appPreloader.vue"
  
  export default {
    name: 'Start',
    components: {
      About,
      Preloader,
      ModalWindow,
      ModalTable,
    },
    props: {},
    data() {
      return {
        url: '',
        newLetters: "",
        img_src: '',
        responseData: null,
        shuffledWord: '',
        hiddenWord: [],
        successWordCounter: 1,
        skipWordsCounter: 1,
        tableTime: '',
        score: 0,
        timer: {
          start: true,
          seconds: '00',
          minutes: '00',
        },
      }
    },
    created() {
      // this.getUrl();
      this.letStartTheGame();
    },
    mounted() {
      setInterval(this.goTimer, 1000);
    },
    computed: {},
    methods: {
      //Получение данных с API
      getData() {
        let randomNumber = Math.floor(Math.random() * (1368 - 1) + 2);
        let url = "https://apidir.pfdo.ru/v1/directory-program-activities/" + randomNumber;
        //44
        console.info("получим данные по ссылке", url);
        return axios
          .get(url)
          .then(response => {
            console.info("получили данные", response);
            return response
          })
          .catch(error => console.log(error));
        
      },
      
      //Добавить полученные данные в компонент
      setData(data) {
        console.info("установим данные", data);
        if (data.data.result_message) {
          if (data.data.result_message === 'Запись не найдена'  || data.data.data.img_src == null) {
            this.letStartTheGame();
          }
          else if(data.data.result_message === 'Полный успех') {
            this.responseData = data.data.data;
            this.img_src = this.responseData.img_src;
            console.log(this.responseData.name);
            return this.responseData.name;
        }
          return 'Ждём';
        }
      },
      
      //Асинхронный запуск игры
      letStartTheGame() {
        this.getData(this.url)
          .catch(error => console.log(error))
          .then(this.setData)
          .then(this.shuffle)
          .catch(error => console.log(error));
      },
      
      //Перемешивание символов в строке
      shuffle(str) {
        console.info("перемешаем");
        let arr = str.split('');
        let j, temp;
        for (let i = arr.length - 1; i > 0; i--) {
          j = Math.floor(Math.random() * (i + 1));
          temp = arr[j];
          arr[j] = arr[i];
          arr[i] = temp;
        }
        return this.shuffledWord = arr.join('');
      },
      
      //Таймер
      goTimer() {
        this.timer.seconds++;
        this.timer.seconds <= 9 ? this.timer.seconds = '0' + this.timer.seconds : this.timer.seconds;
        if (this.timer.seconds === 59) {
          if (this.timer.minutes <= 9) {
            this.timer.minutes = '0' + this.timer.minutes;
          }
          this.timer.minutes++;
          this.timer.seconds = 0;
        }
        if (this.timer.minutes <= 9) {
          this.timer.minutes = '00';
        }
        
      },
      
      //Удаление последней буквы
      deleteLastLetter() {
        this.newLetters = this.newLetters.slice(0, this.newLetters.length - 1);
      },
      
      //Полный сброс отгаданных букв
      deleteInputWord() {
        this.newLetters = "";
      },
      
      //Пропуск слова (пришлось сделать, т.к. слова порой очень непонятные, люди жаловались))
      //При пропуске слова - очки убавляются, пропущенное слово добавляется в массив, игра начинается заного
      skipWord() {
        this.score--;
        
        this.tableTime = this.timer.minutes + ':' + this.timer.seconds;
        this.$store.commit('changeWordTable', {
          table: 'tableSkipWord',
          pushing: {
            '№': this.skipWordsCounter,
            'Слово': this.responseData.name,
            'Время': this.tableTime
          },
        });
        this.skipWordsCounter++;
        
        this.letStartTheGame();
        
      },
      
      //Функция для подстановки букв
      updateHiddenWord(e) {
        this.newLetters += e.target.textContent;
        
        if (this.newLetters.length === this.shuffledWord.length) {
          if (this.newLetters.toUpperCase() === this.responseData.name.toUpperCase()) {
            this.score++;
            this.tableTime = this.timer.minutes + ':' + this.timer.seconds;
            
            this.$store.commit('changeWordTable', {
              table: 'tableSuccessWord',
              pushing: {
                  '№': this.successWordCounter,
                  'Слово': this.responseData.name,
                  'Время': this.tableTime
              },
            });
            this.successWordCounter++;
            this.newLetters = "";
            
            this.$store.commit('changeModalState', 'modalWindow');
          }
        }
      },
    }
  }
</script>

